.proofs__title {
  text-align: center;
  padding: 120px; }

.proof {
  background: #ffffff;
  color: #272727; }
  .proof__header, .proof__content {
    padding: 16px 20px; }
  .proof__header {
    position: relative;
    background: #ffd900;
    font-weight: bold;
    font-size: 18px; }
  .proof__count {
    font-size: 12px; }
  .proof__message {
    font-size: 36px;
    padding-top: 12px; }
  .proof__term {
    padding-bottom: 16px;
    line-height: 1.56; }
  .proof__termTitle {
    width: 42%;
    padding-right: 5px;
    font-weight: bold; }
  .proof__termDescr {
    font-weight: 300; }

.proof__header {
  position: relative; }
  .proof__header:after {
    content: "";
    overflow: hidden;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.03;
    z-index: 0; }

.proof__header.proof1 {
  position: relative; }
  .proof__header.proof1:after {
    background-image: url("./imgs/flow-1.png");
    background-position: center; }

.proof__header.proof2 {
  position: relative; }
  .proof__header.proof2:after {
    background-image: url("./imgs/flow-2.png");
    background-position: 29% 58%;
    opacity: 0.15; }

.proof__header.proof3 {
  position: relative; }
  .proof__header.proof3:after {
    background-image: url("./imgs/flow-3.png");
    background-position: center; }



