.contactFooter {
  text-align: center; }
  @media (min-width: 576px) {
    .contactFooter {
      text-align: left; } }
  .contactFooter__form {
    padding-right: 0.8rem;
    margin-bottom: 2.5rem; }
  .contactFooter__emails {
    padding-left: 0.8rem;
    margin-bottom: 2.5rem; }
  .contactFooter__title {
    display: block;
    font-weight: 600;
    letter-spacing: -.02em;
